:root{
  --primary1:#18c2df;
  --primary2:#1a66ff;
  --bg:#fbfdff;
  --card:#ffffff;
}
body { font-family: Inter, system-ui, -apple-system, "Hiragino Kaku Gothic ProN", "Noto Sans JP", sans-serif; background:var(--bg); margin:0; color:#1b2540;}
.app { max-width:900px; margin:0 auto; padding:20px; }
.header { display:flex; justify-content:space-between; align-items:flex-end; }
.header h1 { font-size:36px; margin:0; font-weight:800; }
.tabs button { margin-left:8px; padding:8px 12px; border-radius:20px; border:none; background:#eef6ff; }
.tabs .active { background:linear-gradient(90deg,var(--primary1),var(--primary2)); color:#fff; }

.container { margin-top:18px; }
.top-row { display:flex; justify-content:space-between; align-items:center; gap:20px; }
.avg-block { background:var(--card); padding:16px; border-radius:12px; box-shadow:0 6px 20px rgba(20,40,80,0.06); }
.avg-block .avg-value { font-size:36px; font-weight:700; margin-top:6px; }
.controls { margin-top:16px; background:transparent; }

.slider-row { margin:12px 0; display:flex; align-items:center; gap:12px; }
.slider-row input[type=range]{ width:70%; }

.recommendation { margin-top:12px; }
.cards { display:flex; gap:12px; margin-top:8px; }
.card { background:var(--card); padding:12px; border-radius:12px; box-shadow:0 6px 18px rgba(20,40,80,0.04); width:140px; text-align:center; }
.card.highlight { border:2px solid var(--primary1); transform:translateY(-4px); }
.emoji { font-size:28px; }
.pill { display:inline-block; padding:8px 14px; border-radius:22px; background:#eef6ff; margin-right:8px; cursor:pointer; }
.pill.selected { background:linear-gradient(90deg,var(--primary1),var(--primary2)); color:white; }

.hand-select { display:flex; gap:8px; margin-top:12px;}
.hand { padding:10px 12px; border-radius:12px; background:#fff; border:1px solid #eef2f6; cursor:pointer; }
.hand.active { box-shadow:0 8px 20px rgba(20,40,80,0.06); }

.actions { margin-top:12px; display:flex; gap:10px; }
.primary { background:linear-gradient(90deg,var(--primary1),var(--primary2)); color:white; padding:12px 18px; border-radius:12px; border:none; cursor:pointer; }
.ghost { padding:10px 14px; border-radius:8px; border:1px solid #dfeaf8; background:white; cursor:pointer; }

.records-table table { width:100%; border-collapse:collapse; margin-top:16px; background:white; border-radius:8px; overflow:hidden; }
.records-table th, .records-table td { padding:10px 12px; text-align:left; border-bottom:1px solid #f1f5f9; }
.del { background:#fff; border:1px solid #dfeaf8; padding:6px 10px; border-radius:8px; cursor:pointer; }
